<style lang="scss" scoped>
  @import "../../assets/scss/List";

  .deploy-range {
    .el-table::before {
      height: 0;
    }
  }
</style>

<template>
  <section class='box'>
    <!---->
    <el-col :span="24" class="search">
      <el-form :inline="true" :model="filters">
        <el-form-item>
          <span class="search-label"></span>
          <el-input v-model="filters.app"></el-input>
        </el-form-item>
        <el-form-item>
          <span class="search-label"></span>
          <el-input v-model="filters.software"></el-input>
        </el-form-item>
        <el-form-item>
          <span class="search-label"></span>
          <el-select v-model="filters.status" filterable>
            <el-option
              v-for="item in statusItems"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <span class="search-label"></span>
          <el-date-picker v-model="filters.timeRange" 
            type="datetimerange"
            placeholder=""
            align="right" 
            class="date-picker" style="width:360px;">
          </el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="getAbilityDeploy"></el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <section class="tools">
      <el-button type="primary" v-on:click="handleAdd"></el-button>
    </section>
    <!---->
    <el-table :data="abilityDeployRecord" stripe border v-loading="loading" element-loading-text='...'>
      <el-table-column prop="deployTime" label="" sortable>
      </el-table-column>
      <el-table-column prop="app" label="" sortable>
      </el-table-column>
      <el-table-column prop="software" label="" sortable>
      </el-table-column>
      <el-table-column prop="canUpdateTime" label="" sortable>
      </el-table-column>
      <el-table-column prop="deployRange" label="" sortable>
        <template slot-scope='scope'>
          {{scope.row.deployRange}}
          (<span class="link-type" @click="deployRangeDetail(scope.row)">
            {{scope.row.deployRangeCount}}
          </span>)
        </template>
      </el-table-column>      
      <el-table-column prop="status" label="" sortable :formatter='formatStatus'>
      </el-table-column>
      <el-table-column label=''>
        <template slot-scope='scope'>
          <el-button type='danger' size='small' @click='removeRecord(scope.$index, scope.row)'>
            
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <!---->
    <el-col :span='24' class="pagination">
      <!-- size-change  -->
      <!-- current-change  -->
      <el-pagination
        @size-change='handleSizeChange'
        @current-change='handleCurrentChange'
        :current-page='pagination.currentPage'
        :page-sizes='[10, 20, 30, 40]'
        :page-size='pagination.limit'
        :total='pagination.total'
        layout='total, sizes, prev, pager, next, jumper'
        style='float:right;'>
      </el-pagination>
    </el-col>
    <!---->
    <el-dialog title='' :visible.sync='addFormVisible' :close-on-click-modal='false' width='20%'>
      <el-form :model='addForm' label-width='80px' ref='addForm'>
        <el-form-item label=''>
          <el-select v-model="addForm.app" filterable placeholder="">
            <el-option
              v-for="item in appItems"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=''>
          <el-select v-model="addForm.software" filterable placeholder="">
            <el-option
              v-for="item in softwareItems"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <p>
        <el-button type='primary' style="width:100%;">
          
        </el-button>
      </p>
      <p>
        <router-link :to="{ path:'/ec/ability-deploy/software', query: { app: addForm.app, software: addForm.software }}">
          <el-button type='primary' style="width:100%;">
            
          </el-button>
        </router-link>
      </p>
    </el-dialog>
    <!---->
    <el-dialog title="" :visible.sync="deployFormVisible" class="deploy-range">
      <el-form :inline="true" :model="rangeFilters">
        <el-form-item>
          <span class="search-label"></span>
          <el-input v-model="rangeFilters.app" style="width:240px;"></el-input>
        </el-form-item>
        <el-form-item>
          <span class="search-label"></span>
          <el-select v-model="rangeFilters.status" filterable>
            <el-option 
              v-for="item in rangeStatusItems"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="getDeployRange"></el-button>
        </el-form-item>
      </el-form>
      <el-table :data="deployRangeRecord" stripe border v-loading="loading" element-loading-text='...' style="overflow-y: scroll;max-height: 440px;">
        <el-table-column prop="logicGroup" label="" sortable>
        </el-table-column>
        <el-table-column prop="status" label="" sortable :formatter='formatRangeStatus'>
        </el-table-column>
      </el-table>
      <!---->
      <el-col :span='24' class="pagination">
        <!-- size-change  -->
        <!-- current-change  -->
        <el-pagination
          @size-change='handleRangeSizeChange'
          @current-change='handleRangeCurrentChange'
          :current-page='rangePagination.currentPage'
          :page-sizes='[10, 20, 30, 40]'
          :page-size='rangePagination.limit'
          :total='rangePagination.total'
          layout='total, sizes, prev, pager, next, jumper'
          style='float:right;'>
        </el-pagination>
      </el-col>
      <div class="clearfix"></div>
    </el-dialog>
  </section>
</template>

<script>
  import abilityDeployList from '@/api/abilityDeploy'
  import moment from 'moment'

  export default {
    data () {
      return {
        extend: false,
        loading: false,
        abilityDeployRecord: [],
        deployRangeRecord: [],
        filters: {
          app: '',
          software: '',
          status: 'all',
          timeRange: ''
        },
        rangeFilters: {
          app: '',
          status: 'all'
        },
        statusItems: [{
          value: 'all',
          label: ''
        }, {
          value: 'effected',
          label: ''
        }, {
          value: 'cancel',
          label: ''
        }],
        rangeStatusItems: [{
          value: 'all',
          label: ''
        }, {
          value: 'deployed',
          label: ''
        }, {
          value: 'noDepoly',
          label: ''
        }],
        // 
        appItems: [{
          value: 'all',
          label: ''
        }],
        // 
        softwareItems: [{
          value: 'all',
          label: ''
        }],
        // 
        pagination: {
          limit: 10, // 
          start: 0,
          currentPage: 1,
          total: 0
        },
        // 
        rangePagination: {
          limit: 10, // 
          start: 0,
          currentPage: 1,
          total: 0
        },
        // 
        addFormVisible: false,
        addLoading: false,
        addForm: {
          app: '',
          software: ''
        },
        deployFormVisible: false
      }
    },
    mounted () {
      // 
      this.getAbilityDeploy()
    },
    methods: {
      // 
      formatStatus: function (row) {
        let state = {
          'all': '',
          'effected': '',
          'cancel': ''
        }
        return state[row.status]
      },
      // 
      formatRangeStatus: function (row) {
        let state = {
          'deployed': '',
          'noDepoly': ''
        }
        return state[row.status]
      },
      // 
      handleSizeChange: function (val) {
        this.pagination.limit = val
        this.getAbilityDeploy(this.pagination.start, this.pagination.limit)
      },
      // 
      handleCurrentChange: function (val) {
        this.pagination.start = (val - 1) * this.pagination.limit
        this.getAbilityDeploy(this.pagination.start, this.pagination.limit)
      },
      // 
      handleRangeSizeChange: function (val) {
        this.rangePagination.limit = val
        this.getDeployRange(this.rangePagination.start, this.rangePagination.limit)
      },
      // 
      handleRangeCurrentChange: function (val) {
        this.rangePagination.start = (val - 1) * this.rangePagination.limit
        this.getDeployRange(this.rangePagination.start, this.rangePagination.limit)
      },
      // 
      getAbilityDeploy: function () {
        this.loading = true
        let params = {
          'app': this.filters.app,
          'software': this.filters.software,
          'status': this.filters.status,
          'timeRange': this.filters.timeRange,
          'limit': this.pagination.limit,
          'start': this.pagination.start
        }

        abilityDeployList.getAbilityDeploy(params).then((res) => {
          this.loading = false

          if (res.data.success === true) {
            this.pagination.total = res.data.value.totalCount
            this.abilityDeployRecord = res.data.value.rows
          } else {
            this.$message({
              message: res.data.msg,
              type: 'error'
            })
          }
        })
      },
      // 
      removeRecord: function (index, row) {
        this.$confirm('?', '', {
          type: 'warning'
        }).then(() => {
          this.listLoading = true
          let id = row.id

          abilityDeployList.removeDeployRecord(id).then((res) => {
            this.listLoading = false

            if (res.data.success === true) {
              this.$message({
                message: res.data.msg,
                type: 'success'
              })
              this.getAbilityDeploy()
            } else {
              this.$message({
                message: res.data.msg,
                type: 'error'
              })
            }
          })
        })
      },
      // 
      getDeployRange: function (index, row) {
        this.loading = true
        let params = {
          'app': this.rangeFilters.app,
          'status': this.rangeFilters.status,
          'limit': this.rangePagination.limit,
          'start': this.rangePagination.start
        }

        abilityDeployList.getDeployRangeList(params).then((res) => {
          this.loading = false

          if (res.data.success === true) {
            this.rangePagination.total = res.data.value.totalCount
            this.deployRangeRecord = res.data.value.rows
          } else {
            this.$message({
              message: res.data.msg,
              type: 'error'
            })
          }
        })
      },
      // 
      handleAdd: function (index, row) {
        this.addFormVisible = true
        this.addForm = {
          app: '',
          software: ''
        }
      },
      // 
      deployRangeDetail: function (index, row) {
        this.deployFormVisible = true
        this.getDeployRange()
      }
    }
  }
</script>



