import axios from 'axios'
import baseUrl from '@/api/base'

const userList = {
  // 
  getLogicData: params => {
    return axios({
      method: 'get',
      url: `${baseUrl}/logicData`,
      params: params
    }).then(res => {
      return res
    }).catch(err => {
      console.log(': ', err)
      return err
    })
  },
  // 
  getUsers: params => {
    return axios({
      method: 'get',
      url: `${baseUrl}/users`,
      params: params
    }).then(res => {
      return res
    }).catch(err => {
      console.log(': ', err)
      return err
    })
  },
  // 
  getUser: params => {
    return axios({
      method: 'get',
      url: `${baseUrl}/user/${params}`
    }).then(res => {
      return res
    }).catch(err => {
      console.log(': ', err)
      return err
    })
  },
  // 
  removeUser: params => {
    return axios.delete(`${baseUrl}/user/${params}`).then(res => {
      return res
    }).catch(err => {
      console.log(': ', err)
      return err
    })
  },
  // 
  editUser: params => {
    return axios.put(`${baseUrl}/user/${params.id}`, params).then(res => {
      return res
    }).catch(err => {
      console.log(': ', err)
      return err
    })
  },
  // 
  addUser: params => {
    return axios.post(`${baseUrl}/user`, params).then(res => {
      return res
    }).catch(err => {
      console.log(': ', err)
      return err
    })
  },
  // 
  batchRemoveUser: params => {
    return axios.post(`${baseUrl}/user/batch`, params).then(res => {
      return res
    }).catch(err => {
      console.log(': ', err)
      return err
    })
  }
}

export default userList



