<style lang="scss" scoped>
  @import "../../assets/scss/List";

  .app-table {
    height: 680px;
    overflow-y: auto;
  }
  .operation-button {
    margin: 0;
  }
  .app-input {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
  }
  .dialog-footer {
    text-align: center;
  }
  .depend-form {
    max-height: 220px;
    overflow-y: auto;
  }
</style>

<template>
  <section class='box'>
    <!---->
    <section class="tools">
      <el-button type="primary" @click="handleAddApp"></el-button>
    </section>
    <!---->
    <section class="app-table">
      <el-table :data="apps" stripe border v-loading="loading" element-loading-text='...'>
        <el-table-column prop="app" label="" sortable>
        </el-table-column>
        <el-table-column prop="software" label="" sortable>
        </el-table-column>
        <el-table-column prop="process" label="" sortable>
        </el-table-column>
        <el-table-column prop="port" label="" sortable>
        </el-table-column>
        <el-table-column prop="startCommand" label="" sortable>
        </el-table-column>
        <el-table-column prop="stopCommand" label="" sortable>
        </el-table-column>
        <el-table-column prop="status" label="" sortable :formatter='formatStatus'>
        </el-table-column>
        <el-table-column prop="template" label="" sortable>
        </el-table-column>
        <el-table-column prop="image" label="" sortable>
        </el-table-column>
        <el-table-column label='' width="368">
          <template slot-scope='scope'>
            <el-button size='small' class="operation-button" @click='handleEditApp(scope.$index, scope.row)'></el-button>
            <el-button size='small' class="operation-button" @click='handleDependConfig(scope.row)'></el-button>
            <el-button size='small' class="operation-button" @click='handleUpgradeApp'></el-button>
            <router-link :to="{ path:'/ec/ability-deploy/software', query: { app: scope.row.id, software: scope.row.id }}">
              <el-button size='small' class="operation-button"></el-button>
            </router-link>
            <el-button type='danger' size='small' @click='removeApp(scope.$index, scope.row)' class="operation-button">
              
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </section>
    <!---->
    <el-dialog title='' :visible.sync='addFormVisible' :close-on-click-modal='false' width="40%">
      <el-form :model='addForm' label-width='100px' label-position='left' :rules='addFormRules' ref='addForm'>
        <el-form-item label='' class="app-input" prop="app">
          <el-input v-model='addForm.app'></el-input>
        </el-form-item>
        <el-form-item label='' class="app-input">
          <el-upload
            class="upload-demo"
            ref="upload"
            action="https://jsonplaceholder.typicode.com/posts/"
            :on-preview="submitPreview"
            :on-remove="submitRemove"
            :file-list="fileList"
            :auto-upload="false">
            <el-button slot="trigger" size="small" type="primary"></el-button>
            <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload"></el-button>
            <div slot="tip" class="el-upload__tip">rpm/gz</div>
          </el-upload>
        </el-form-item>
        <el-form-item label='' class="app-input" prop="process">
          <el-input v-model='addForm.process'></el-input>
        </el-form-item>
        <el-form-item label='' class="app-input" prop="port">
          <el-input v-model='addForm.port'></el-input>
        </el-form-item>
        <el-form-item label='' class="app-input" prop="startCommand">
          <el-input v-model='addForm.startCommand'></el-input>
        </el-form-item>
        <el-form-item label='' class="app-input" prop="stopCommand">
          <el-input v-model='addForm.stopCommand'></el-input>
        </el-form-item>
        <el-form-item label='' class="app-input" prop="status">
          <el-select v-model="addForm.status" filterable placeholder="">
            <el-option
              v-for="item in statusItems"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot='footer' class='dialog-footer'>
        <el-button @click.native='addFormVisible = false'></el-button>
        <el-button type='primary' @click="addApp('addForm')" :loading='addLoading'></el-button>
      </div>
    </el-dialog>
    <!---->
    <el-dialog title='' :visible.sync='editFormVisible' :close-on-click-modal='false' width="40%">
      <el-form :model='editForm' label-width='100px' label-position='left' :rules='addFormRules' ref='editForm'>
        <el-form-item label='' class="app-input" prop="app">
          <el-input v-model='editForm.app'></el-input>
        </el-form-item>
        <el-form-item label='' class="app-input" prop="process">
          <el-input v-model='editForm.process'></el-input>
        </el-form-item>
        <el-form-item label='' class="app-input" prop="port">
          <el-input v-model='editForm.port'></el-input>
        </el-form-item>
        <el-form-item label='' class="app-input" prop="startCommand">
          <el-input v-model='editForm.startCommand'></el-input>
        </el-form-item>
        <el-form-item label='' class="app-input" prop="stopCommand">
          <el-input v-model='editForm.stopCommand'></el-input>
        </el-form-item>
        <el-form-item label='' class="app-input" prop="status">
          <el-select v-model="editForm.status" filterable placeholder="">
            <el-option
              v-for="item in statusItems"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot='footer' class='dialog-footer'>
        <el-button @click.native='editFormVisible = false'></el-button>
        <el-button type='primary' @click="editApp('editForm')" :loading='editLoading'></el-button>
      </div>
    </el-dialog>
    <!---->
    <el-dialog title='' :visible.sync='dependFormVisible' :close-on-click-modal='false' width="84%">
      <p style="margin-top: -20px;"></p>
      <section class="depend-form">
        <el-table :data="vmData" stripe border v-loading="loading" element-loading-text='...'>
          <el-table-column label="" width="80" align="center">
            <template slot-scope="scope">
              <el-radio v-model="vmRadio" :label="scope.$index+1" @change.native="handleVmSelect(scope.row)"></el-radio>
            </template>
          </el-table-column>
          <el-table-column prop="name" label="" sortable>
          </el-table-column>
          <el-table-column prop="cpu" label="cpu" sortable>
          </el-table-column>
          <el-table-column prop="memory" label="" sortable>
          </el-table-column>
          <el-table-column prop="disk" label="" sortable>
          </el-table-column>
        </el-table>
      </section>
      <p></p>
      <section class="depend-form">
        <el-table :data="imageData" stripe border v-loading="loading" element-loading-text='...'>
          <el-table-column label="" width="80" align="center">
            <template slot-scope="scope">
              <el-radio v-model="imageRadio" :label="scope.$index+1" @change.native="handleImageSelect(scope.row)"></el-radio>
            </template>
          </el-table-column>
          <el-table-column prop="name" label="" sortable>
          </el-table-column>
          <el-table-column prop="type" label="" sortable>
          </el-table-column>
          <el-table-column prop="format" label="" sortable>
          </el-table-column>
          <el-table-column prop="diskMin" label="" sortable>
          </el-table-column>
          <el-table-column prop="imageMin" label="" sortable>
          </el-table-column>
          <el-table-column prop="status" label="" sortable>
          </el-table-column>
          <el-table-column prop="extend" label="" sortable>
          </el-table-column>
        </el-table>
      </section>
      <div slot='footer' class='dialog-footer'>
        <el-button @click.native='dependFormVisible = false'></el-button>
        <el-button type='primary' @click='dependConfigSubmit' :loading='dependLoading'></el-button>
      </div>
    </el-dialog>
    <!---->
    <el-dialog title='' :visible.sync='upgradeFormVisible' :close-on-click-modal='false' width="40%">
      <el-form :model='upgradeForm' label-width='100px' label-position='left' ref='upgradeForm'>
        <el-form-item label='' class="app-input">
          <el-upload
            class="upload-demo"
            ref="upload"
            action="https://jsonplaceholder.typicode.com/posts/"
            :on-preview="submitPreview"
            :on-remove="submitRemove"
            :file-list="fileList"
            :auto-upload="false">
            <el-button slot="trigger" size="small" type="primary"></el-button>
            <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload"></el-button>
            <div slot="tip" class="el-upload__tip">rpm/gz</div>
          </el-upload>
        </el-form-item>
      </el-form>
      <div slot='footer' class='dialog-footer'>
        <el-button @click.native='upgradeFormVisible = false'></el-button>
        <el-button type='primary' @click='upgradeApp' :loading='upgradeLoading'></el-button>
      </div>
    </el-dialog>
  </section>
</template>

<script>
  import applicationDeploy from '@/api/applicationDeploy'

  export default {
    data () {
      return {
        extend: false,
        loading: false,
        apps: [],
        statusItems: [{
          value: 1,
          label: ''
        }, {
          value: 0,
          label: ''
        }],
        // 
        addFormVisible: false, // 
        addLoading: false,
        addForm: {
          app: '',
          software: '',
          process: '',
          port: '',
          startCommand: '',
          stopCommand: '',
          status: ''
        },
        addFormRules: {
          app: [{ required: true, message: '', trigger: 'blur,change' }],
          process: [{ required: true, message: '', trigger: 'blur,change' }],
          port: [
            { required: true, message: '', trigger: 'blur,change' }
          ],
          startCommand: [{ required: true, message: '', trigger: 'blur,change' }],
          stopCommand: [{ required: true, message: '', trigger: 'blur,change' }],
          status: [{ required: true, message: '', trigger: 'blur,change' }]
        },
        fileList: [],
        // 
        upgradeFormVisible: false,
        upgradeLoading: false,
        upgradeForm: {},
        // 
        editFormVisible: false,
        editLoading: false,
        editForm: {},
        // 
        dependFormVisible: false,
        dependLoading: false,
        vmData: [],
        imageData: [],
        vmRadio: '',
        imageRadio: '',
        dependSelect: {}
      }
    },
    mounted () {
      this.getApps()
    },
    methods: {
      // 
      formatStatus: function (row) {
        let status = {
          '0': '',
          '1': ''
        }
        return status[row.status]
      },
      // 
      selsChange: function (sels) {
        this.sels = sels
      },
      // 
      getApps: function () {
        this.loading = true

        applicationDeploy.getApps().then((res) => {
          this.loading = false

          if (res.data.success === true) {
            this.apps = res.data.value.rows
          } else {
            this.$message({
              message: res.data.msg,
              type: 'error'
            })
          }
        })
      },
      // 
      editApp: function () {
        this.$refs.editForm.validate((valid) => {
          // 
          if (valid) {
            this.$confirm('', '', {}).then(() => {
              this.editLoading = true
              let params = Object.assign({}, this.editForm)

              applicationDeploy.editApp(params).then((res) => {
                this.editLoading = false

                if (res.data.success === true) {
                  this.$message({
                    message: res.data.msg,
                    type: 'success'
                  })
                  this.editFormVisible = false
                  this.getApps()
                } else {
                  this.$message({
                    message: res.data.msg,
                    type: 'error'
                  })
                }
              })
            })
          }
        })
      },
      // 
      addApp: function (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$confirm('', '', {}).then(() => {
              this.addLoading = true
              let params = Object.assign({}, this.addForm)

              applicationDeploy.addApp(params).then((res) => {
                this.addLoading = false
                this.$message({
                  message: '',
                  type: 'success'
                })
                this.$refs['addForm'].resetFields()
                this.addFormVisible = false
                this.getApps()
              })
            })
          }
        })
      },
      // 
      upgradeApp: function () {
        this.$confirm('', '', {}).then(() => {
          this.upgradeLoading = true
          let params = Object.assign({}, this.upgradeForm)

          applicationDeploy.upgradeApp(params).then((res) => {
            this.upgradeLoading = false
            this.$message({
              message: '',
              type: 'success'
            })
            this.$refs['upgradeForm'].resetFields()
            this.upgradeFormVisible = false
            this.getApps()
          })
        })
      },
      // 
      dependConfigSubmit: function () {
        this.$confirm('', '', {}).then(() => {
          this.dependLoading = true
          let params = this.dependSelect
          console.log(params)

          applicationDeploy.dependConfigSubmit(params).then((res) => {
            this.dependLoading = false
            this.$message({
              message: '',
              type: 'success'
            })
            this.dependFormVisible = false
            this.getApps()
          })
        })
      },
      // 
      handleVmSelect: function (row) {
        this.dependSelect['vm'] = row
      },
      // 
      handleImageSelect: function (row) {
        this.dependSelect['image'] = row
      },
      // 
      removeApp: function (index, row) {
        this.$confirm('?', '', {
          type: 'warning'
        }).then(() => {
          this.listLoading = true
          let id = row.id

          applicationDeploy.removeApp(id).then((res) => {
            this.listLoading = false

            if (res.data.success === true) {
              this.$message({
                message: res.data.msg,
                type: 'success'
              })
              this.getApps()
            } else {
              this.$message({
                message: res.data.msg,
                type: 'error'
              })
            }
          })
        })
      },
      // 
      handleAddApp: function (index, row) {
        this.addFormVisible = true
        this.addForm = {
          app: '',
          software: '',
          process: '',
          port: '',
          startCommand: '',
          stopCommand: '',
          status: ''
        }
      },
      // 
      handleDependConfig: function (row) {
        this.dependFormVisible = true
        let id = row.id

        applicationDeploy.getDependConfig(id).then((res) => {
          this.vmData = res.data.value.vmData
          this.imageData = res.data.value.imageData
        })
      },
      // 
      handleUpgradeApp: function (row) {
        this.upgradeFormVisible = true
      },
      // 
      handleEditApp: function (index, row) {
        this.editFormVisible = true
        this.editForm = Object.assign({}, row)
      },
      // 
      submitUpload: function () {
        this.$refs.upload.submit()
      },
      submitRemove: function (file, fileList) {
        console.log(file, fileList)
      },
      submitPreview: function (file) {
        console.log(file)
      }
    }
  }
</script>



