import axios from 'axios'
import baseUrl from '@/api/base'

const applicationDeploy = {
  // 
  getApps: params => {
    return axios({
      method: 'get',
      url: `${baseUrl}/getApps`,
      params: params
    }).then(res => {
      return res
    }).catch(err => {
      console.log(': ', err)
      return err
    })
  },
  // 
  addApp: params => {
    return axios({
      method: 'get',
      url: `${baseUrl}/applicationDeploy/addApp`,
      params: params
    }).then(res => {
      return res
    }).catch(err => {
      console.log(': ', err)
      return err
    })
  },
  // 
  removeApp: params => {
    return axios.delete(`${baseUrl}/applicationDeploy/removeApp/${params}`).then(res => {
      return res
    }).catch(err => {
      console.log(': ', err)
      return err
    })
  },
  // 
  upgradeApp: params => {
    return axios({
      method: 'post',
      url: `${baseUrl}/applicationDeploy/upgradeApp`,
      params: params
    }).then(res => {
      return res
    }).catch(err => {
      console.log(': ', err)
      return err
    })
  },
  // 
  editApp: params => {
    return axios({
      method: 'post',
      url: `${baseUrl}/applicationDeploy/editApp`,
      params: params
    }).then(res => {
      return res
    }).catch(err => {
      console.log(': ', err)
      return err
    })
  },
  // 
  getDependConfig: params => {
    return axios({
      method: 'post',
      url: `${baseUrl}/applicationDeploy/getDependConfig`,
      params: params
    }).then(res => {
      return res
    }).catch(err => {
      console.log(': ', err)
      return err
    })
  },
  // 
  dependConfigSubmit: params => {
    return axios({
      method: 'post',
      url: `${baseUrl}/applicationDeploy/dependConfigSubmit`,
      params: params
    }).then(res => {
      return res
    }).catch(err => {
      console.log(': ', err)
      return err
    })
  }
}

export default applicationDeploy



