<style lang="scss" scoped>
  @import "../../assets/scss/List";
</style>

<template>
  <section class='box'>
    <!---->
    <el-col :span="24" class="search">
      <el-form :inline="true" :model="filters">
        <el-form-item>
          <span class="search-label"></span>
          <el-input v-model="filters.name"></el-input>
        </el-form-item>
        <el-form-item>
          <span class="search-label"></span>
          <el-input v-model="filters.enName"></el-input>
        </el-form-item>
        <el-form-item>
          <span class="search-label">VIP</span>
          <el-select v-model="filters.vip" filterable>
            <el-option
              v-for="item in vipItems"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="getUsers"></el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <!---->
    <el-table :data="users" stripe border v-loading="loading" element-loading-text='...'>
      <el-table-column prop="name" label="" sortable align="center">
      </el-table-column>
      <el-table-column prop="email" label="" sortable align="center">
      </el-table-column>
      <el-table-column prop="status" label="" sortable align="center">
      </el-table-column>
      <el-table-column prop="manage" label="" sortable align="center">
      </el-table-column>
      <el-table-column prop="industry" label="" sortable align="center">
      </el-table-column>
      <el-table-column prop="vipLevel" label="VIP" sortable :formatter='formatVIP' align="center">
      </el-table-column>
      <el-table-column label='' width="140" align="center">
        <template slot-scope='scope'>
          <el-button size='small' @click='handleEdit(scope.$index, scope.row)'></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!---->
    <el-col :span='24' class="pagination">
      <!-- size-change  -->
      <!-- current-change  -->
      <el-pagination
        @size-change='handleSizeChange'
        @current-change='handleCurrentChange'
        :current-page='pagination.currentPage'
        :page-sizes='[10, 20, 30, 40]'
        :page-size='pagination.limit'
        :total='pagination.total'
        layout='total, sizes, prev, pager, next, jumper'
        style='float:right;'>
      </el-pagination>
    </el-col>
    <!---->
    <el-dialog title='' v-model='editFormVisible' :close-on-click-modal='false'>
      <el-form :model='editForm' label-width='80px' :rules='editFormRules' ref='editForm'>
        <el-form-item label='' prop='name'>
          <el-input v-model='editForm.name' auto-complete='off'></el-input>
        </el-form-item>
        <el-form-item label=''>
          <el-input v-model='editForm.email'></el-input>
        </el-form-item>
        <el-form-item label='VIP'>
          <el-select v-model="editForm.vipLevel" filterable placeholder="VIP">
            <el-option v-for="item in vipItems" :key="item.value" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot='footer' class='dialog-footer'>
        <el-button @click.native='editFormVisible = false'></el-button>
        <el-button type='primary' @click.native='editUser' :loading='editLoading'></el-button>
      </div>
    </el-dialog>
  </section>
</template>

<script>
  // apiapi@ src 
  import userList from '@/api/list'

  export default {
    data () {
      return {
        loading: false,
        users: [],
        filters: {
          name: '',
          enName: '',
          vip: 0
        },
        vipItems: [{
          value: 0,
          label: ''
        }, {
          value: 1,
          label: 'VIP1'
        }, {
          value: 2,
          label: 'VIP2'
        }, {
          value: 3,
          label: 'VIP3'
        }],
        // 
        pagination: {
          limit: 20, // 
          start: 0,
          currentPage: 1,
          total: 0
        },
        // 
        editFormVisible: false, // 
        editLoading: false,
        editForm: {
          name: '',
          email: '',
          status: undefined,
          vipLevel: undefined,
          createTime: ''
        },
        editFormRules: {
          name: [
            { required: true, message: '', trigger: 'blur' }
          ]
        }
      }
    },
    mounted () {
      // 
      this.getUsers()
    },
    methods: {
      // vip
      formatVIP: function (row) {
        let vip = {
          '0': '',
          '1': 'VIP1',
          '2': 'VIP2',
          '3': 'VIP3'
        }
        return vip[row.vipLevel]
      },
      // 
      handleSizeChange: function (val) {
        this.pagination.limit = val
        this.getUsers(this.pagination.start, this.pagination.limit)
      },
      // 
      handleCurrentChange: function (val) {
        this.pagination.start = (val - 1) * this.pagination.limit
        this.getUsers(this.pagination.start, this.pagination.limit)
      },
      // 
      getUsers: function () {
        var self = this
        this.loading = true
        let params = {
          name: this.filters.name,
          enName: this.filters.enName,
          limit: this.pagination.limit,
          start: this.pagination.start,
          vipLevel: this.filters.vip
        }

        userList.getUsers(params).then((res) => {
          this.loading = false

          if (res.data.success === true) {
            this.pagination.total = res.data.value.totalCount
            this.users = res.data.value.rows
          } else {
            this.$message({
              message: res.data.msg,
              type: 'error'
            })
          }
        })
      },
      // 
      editUser: function () {
        this.$refs.editForm.validate((valid) => {
          // 
          if (valid) {
            this.$confirm('', '', {}).then(() => {
              this.editLoading = true
              let para = Object.assign({}, this.editForm)

              userList.editUser(para).then((res) => {
                this.editLoading = false

                if (res.data.success === true) {
                  this.$message({
                    message: res.data.msg,
                    type: 'success'
                  })
                  this.$refs['editForm'].resetFields()
                  this.editFormVisible = false
                  this.getUsers()
                } else {
                  this.$message({
                    message: res.data.msg,
                    type: 'error'
                  })
                }
              })
            })
          }
        })
      },
      // 
      handleEdit: function (index, row) {
        this.editFormVisible = true
        this.editForm = Object.assign({}, row)
      }
    }
  }
</script>



